:root {
    --background: rgb(12, 29, 36);
    --neon-light-pink: rgb(255, 230, 255);
    --neon-med-pink: rgb(255, 101, 189);
    --neon-dark-pink: rgb(255, 36, 131);
    --nav-pink: rgb(255, 36, 131);
    --nav-links-toggler: rgba(255, 255, 255, 0.822);
  }
  
  /*
  *
  * =====================
  * GENERAL
  * =====================
  *
  */
  
.navbar {
  display: flex;
  width: 100vw;
  background: var(--background);
  font-size: .8rem;
  padding: 5px;
}

#navbarSupportedContent {
  text-align: center;
}

.navbar-brand span {
  font-family: 'Poppins', sans-serif;  
  font-size: 1.5em;
}

.navbar-nav {
  padding-right: 4%;
}

a.nav-link {
  font-family: 'Poppins', sans-serif;
  color: var(--nav-links-toggler); 
  padding-right: 10%;
}

a.nav-link:hover {
  color: var(--nav-pink);
}

.navbar-toggler {
  color: var(--nav-links-toggler);
  background-color: white;
  /* margin-right: 10%; */
}

@media only screen and (min-width: 481px) {
  a.nav-link {
      font-size: .8em;
      margin-right: 0.8em;
  }
}

@media only screen and (min-width: 576px) {
  .navbar {
    margin-left: 0;
    margin-right: 5%;
  }
}

@media only screen and (min-width: 660) {
  .navbar-nav {
    font-size: 2rem;
  }
}

@media only screen and (min-width: 768px) {
  .navbar-brand span {
    font-size: 1.4em;
  }
  a.nav-link {
    font-size: 1em;
    margin-right: 0.8em;
  }
}

@media only screen and (min-width: 1024px) {
  .navbar-brand span {
    font-size: 1.6em;
  }
  a.nav-link {
    font-size: 1.1em;
    margin-right: 0.8em;
  }
}
  
/*
*
* =====================
* NEON NAME
* =====================
*
*/

.neon-text {
  margin-left: 8%;
}

.g {
  padding-left: 0.5em;
}

@keyframes blink {
  0%,
  22%,
  30%,
  75% {
    color: var(--neon-light-pink);
    text-shadow: 0 0 0.6rem var(--neon-light-pink), 0 0 1.5rem var(--neon-med-pink),
      -0.2rem 0.1rem 1rem var(--neon-med-pink), 0.2rem 0.1rem 1rem var(--neon-med-pink),
      0 -0.5rem 2rem var(--neon-dark-pink), 0 0.5rem 3rem var(--neon-dark-pink);
  }
  28%,
  33% {
    color: var(--neon-med-pink);
    text-shadow: none;
  }
  82%,
  97% {
    color: var(--neon-dark-pink);
    text-shadow: none;
  }
}

.flicker {
  animation: shine 2s forwards, blink 10s 2s infinite;
}

.fast-flicker {
  animation: shine 2s forwards, blink 8s 1s infinite;
}

@keyframes shine {
  0% {
    color: #6b1839;
    text-shadow: none;
  }
  100% {
    color: var(--neon-light-pink);
    text-shadow: 0 0 0.6rem var(--neon-light-pink), 0 0 1.5rem var(--neon-med-pink),
      -0.2rem 0.1rem 1rem var(--neon-med-pink), 0.2rem 0.1rem 1rem var(--neon-med-pink),
      0 -0.5rem 2rem var(--neon-dark-pink), 0 0.5rem 3rem var(--neon-dark-pink);
  }
}

